
@import url(./style/sprite.css);
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
input,
button {
  outline: medium;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
  color: #343643;
}
a:active {
    background-color: transparent;
}
html,
body,
#app {
  height: 100%;
  color: #343643;
  font-size: 13px;
  font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Arial, sans-serif;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
@media only screen and (min-width: 414px) {
html {
    font-size: 55px !important;
}
}
body {
  overflow-x: hidden;
  overflow-y: auto;
}
body.hidden {
    overflow: hidden !important;
}
.mint-datetime-picker .picker-toolbar {
  border-bottom: 1px solid #EFEFF3 !important;
}
.mint-datetime-picker .mint-datetime-cancel {
  width: auto;
  color: #9194a2;
  text-align: left;
  padding: 0 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.mint-datetime-picker .mint-datetime-confirm {
  width: auto;
  color: #1989fa;
  text-align: right;
  padding: 0 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.mint-popup-bottom p {
  border-bottom: 1px solid #EFEFF3 !important;
}
.mint-popup-bottom .cannel {
  width: auto !important;
  color: #9194a2 !important;
  text-align: left !important;
  padding: 0 16px !important;
  -webkit-box-sizing: border-box !important;
          box-sizing: border-box !important;
}
.mint-popup-bottom .confirm {
  width: auto !important;
  color: #1989fa !important;
  text-align: right !important;
  padding: 0 16px !important;
  -webkit-box-sizing: border-box !important;
          box-sizing: border-box !important;
}
.cicon {
  display: inline-block;
}
.modals-component-container {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 80;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
.modals-component-container.show-head {
    top: 44px;
}
.modals-container {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 1600;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}



