
.modal-loading {
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 1px 0;
  border-radius: 0.1rem;
  height: 1.92rem;
  width: 1.92rem;
  margin-top: -0.86rem;
  margin-left: -0.86rem;
  border-radius: 0.06rem;
  text-align: center;
  background-color: #000;
  z-index: 1601;
}
.modal-loading .loading-content {
    text-align: center;
    font-size: 0.28rem;
    font-weight: 600;
    color: #dbdbdb;
}
.modal-loading .spinner {
    margin: 20px auto 0;
    width: 100%;
    height: 0.96rem;
    position: relative;
    text-align: center;
}
.modal-loading .loadding-1 {
    position: absolute;
    top: 0.1rem;
    margin-left: 0.06rem;
}
.modal-loading .loadding-2 {
    position: relative;
    left: -1px;
    top: 1px;
}
.modal-loading .spinner > div {
    -webkit-animation: bouncedelay 0.8s infinite linear;
    animation: bouncedelay 0.8s infinite linear;
    /* Prevent first frame from flickering when animation starts */
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}
@-webkit-keyframes bouncedelay {
0% {
    -webkit-transform: rotateZ(0deg);
}
100% {
    -webkit-transform: rotateZ(360deg);
}
}
@keyframes bouncedelay {
0% {
    transform: rotateZ(0deg);
    -webkit-transform: rotateZ(0deg);
}
100% {
    transform: rotateZ(360deg);
    -webkit-transform: rotateZ(360deg);
}
}



